// Kendo Overrides
// Note: many of the kendo controls need the content-box box model whereas Bootstrap uses the box-model. Here are manual overrides:
// Also note that calc() should be used carefully as Kendo relies on its LESS files being processed using the strictMath option set to false, thereby often overriding these values
.noshadow {
	.box-shadow(none)!important;
}

.k-autocomplete.k-state-active,
.k-autocomplete.k-state-hover,
.k-dropdown-wrap.k-state-active,
.k-dropdown-wrap.k-state-hover,
.k-multiselect.k-header.k-state-active,
.k-numeric-wrap.k-state-active,
.k-numeric-wrap.k-state-hover,
.k-overflow-anchor:hover,
.k-picker-wrap.k-state-active,
.k-picker-wrap.k-state-hover,
.k-textbox:focus,
.k-textbox:hover {
	.box-shadow(none)!important;
}

.k-checkarea {
	.reset-box-sizing();
}

.k-content {
	background: transparent!important;
	position: relative;
}

.tempgray {
	background: @lightergray!important;
}

.textbuttonstandard {
	float: right;
	padding: 12px;
	cursor: pointer;
	text-transform: uppercase;
	color: @dashblue;
	background: transparent;
	.box-shadow(none);
	border: 0;

	&:focus {
		background-color: @faintgray;
	}
}
// Filter buttons
.k-filter-menu.k-popup {
	.k-button {
		.textbuttonstandard!important;
	}
}
//Fix for text and overlapped sprites showing in grid pagination - do not target Kendo pagination icons
.k-pager-wrap .k-icon:not(.k-i-arrow-w):not(.k-i-arrow-60-left):not(.k-i-arrow-e):not(.k-i-arrow-e):not(.k-i-arrow-60-right):not(.k-i-seek-w):not(.k-i-arrow-end-left):not(.k-i-seek-e):not(.k-i-arrow-end-right) {
	width: 14px;
	height: 14px;
	text-indent: -9999px;
}
// Grid - don't target Kendo pagination arrows due to alignment issue
.k-grid .k-icon:not(.k-i-arrow-w):not(.k-i-arrow-60-left):not(.k-i-arrow-e):not(.k-i-arrow-e):not(.k-i-arrow-60-right):not(.k-i-seek-w):not(.k-i-arrow-end-left):not(.k-i-seek-e):not(.k-i-arrow-end-right) {
	text-indent: -999px;
}

//Tree List Kendo icon removal


// Override Kendo icons where Quorum icons are used
.k-upload-status .k-icon.k-i-close,
.k-window .k-icon.k-i-window-maximize,
.k-window .k-icon.k-i-window-restore,
.k-grid .k-icon.k-i-search,
.k-header .k-grid .k-icon.k-i-refresh,
.k-header .k-grid .k-icon.k-i-arrow-60-down,
.k-header .treeviewholder .k-icon,
.k-header .k-i-reload,
.k-scheduler .k-icon,
.k-reorder-clue .k-icon,
.k-grid .k-i-collapse,
.k-grid .k-i-expand,
.k-grid .k-i-reload,
.k-grid .k-i-sort-asc-sm,
.k-grid .k-i-sort-desc-sm,
.k-grid .k-i-warning,
.k-icon.k-i-arrow-60-up,
.k-icon.k-i-arrow-60-down,
.k-grid .k-i-filter-clear {	
	&:before {
		text-indent: -9999px!important;
	}
}

.k-scheduler .k-i-reload {
	width: 18px;
	height: 18px;
  }

.k-grid .k-icon.k-i-filter:before {
	display: none;
}

.k-grid .k-grid-filter .k-icon.k-i-filter {
	opacity: 0.5;
	&:hover {
		opacity: 0.84;
	}
}
// Grid icons
.k-grid .k-icon:not(.k-i-filter) {
	width: 14px;
	height: 14px;
}

.k-grid .k-filtercell .k-autocomplete .k-i-close {
	position: absolute;
	top: 14px;
	z-index: 999;
	margin-right: 0 !important;
}

.k-grid .k-filtercell .k-autocomplete input {
	padding-right: 16px !important;
}

.k-grid .k-combobox-clearable .k-i-close {
	position: absolute;
	top: 14px !important;
	z-index: 999;
	right: 22px !important;
}

.k-grid .k-dropdown-wrap {
	padding: 0 2.771em 0 0;
}

.k-grid .k-textbox .k-i-close {
	position: absolute;
	right: 0 !important;
	top: 19px !important;
}

.k-grid td .k-autocomplete .k-icon.k-i-loading {
    top: 12px;
}

// Kendo Grid pagination page popup interferes with horizontal scroll bar of grid - this is a fix
@media(max-width:1024px) {
	.k-ff .k-grid .k-pager-numbers,
	.k-ff .k-pager-numbers,
	.k-ie11 .k-grid .k-pager-numbers,
	.k-ie11 .k-pager-numbers,
	.k-webkit .k-grid .k-pager-numbers,
	.k-webkit .k-pager-numbers {
		height: auto;
	}

	.k-ff .k-pager-numbers.k-state-expanded,
	.k-ie11 .k-pager-numbers.k-state-expanded,
	.k-webkit .k-pager-numbers.k-state-expanded {
		height: auto;
	}

	.k-ff .k-pager-numbers .k-current-page .k-link,
	.k-ie11 .k-pager-numbers .k-current-page .k-link,
	.k-webkit .k-pager-numbers .k-current-page .k-link {
		background-color: transparent!important;
	}

	.k-ff .k-pager-numbers .k-current-page:hover .k-link,
	.k-ie11 .k-pager-numbers .k-current-page:hover .k-link,
	.k-webkit .k-pager-numbers .k-current-page:hover .k-link {
		border: 0;
	}

	.k-ff .k-pager-numbers .k-current-page .k-link,
	.k-ie11 .k-pager-numbers .k-current-page .k-link,
	.k-webkit .k-pager-numbers .k-current-page .k-link {
		.box-shadow(none)!important;
		border: 0;
	}

	.gridholder .k-pager-numbers .k-link,
	.gridholder .k-pager-numbers .k-state-selected {
		border: 0!important;
	}
}
// Checkbox
.k-checkbox-label,
.k-radio-label {
	display: inline;
}
// Copy agreement checkbox fix
#TreeListCtrl label.k-checkbox-label.k-input {
	position: relative !important;
	right: initial;
	top: initial;
}
// Kendo editor dialog
.k-editor-dialog {
	.k-input[type="text"],
	.k-input[type="number"],
	.k-textbox,
	.k-picker-wrap .k-input,
	.k-button,
	.k-textbox > input {
		border: 0;
		-webkit-border-radius: 0;
		-moz-border-radius: 0;
		border-radius: 0;
		border-bottom: 1px solid @mediumgray;

		&:focus {
			border: 0;
			border-bottom: 1px solid @dashblue;
			background-position: bottom center, bottom center!important;
		}
	}

	.k-dialog-close.k-button,
	.k-dialog-insert.k-button.k-primary {
		.textbuttonstandard!important;
	}
}
// Tab strip
.k-tabstrip .k-tabstrip-items {
	background: @lightergray;
}

.k-state-active a.k-link {
	color: @dashblue!important;
	font-weight: bold; // text color of active tab
}

.k-tabstrip-items .k-loading {
	border-width: 2px!important;
}
// Kendo Sprites - May be best to overwrite the main Kendo Style to avoid 2 queries
// K-inputs
.k-input,
input.k-textbox {
	padding: 0 0 0.25rem 0 !important; // Use REM to prevent variable padding from parent EM settings
}

.k-autocomplete .k-input,
.k-dropdown-wrap .k-input,
.k-numeric-wrap .k-input,
.k-picker-wrap .k-input,
.k-selectbox .k-input,
.k-textbox > input,
.q-form-control-wrapper > .k-input,
.q-form-control-wrapper > .tooltipHolder > .k-input,
input.k-textbox {
	text-indent: 0;
	height: 1.875em;
	line-height: 1.875em;
}

.k-autocomplete,
.k-colorpicker,
.k-combobox,
.k-datepicker,
.k-datetimepicker,
.k-dropdown,
.k-numerictextbox,
.k-selectbox,
.k-textbox,
.k-timepicker,
.k-toolbar .k-split-button {
	width: 100%!important;
}
//Numberic text box
.k-numerictextbox {
	 .k-link {
		height: 1em;
	}
}
